import {
	ajax,
} from '@/static/js/base.js'
import api from '@/static/js/api.js'
import {
	mapGetters
} from 'vuex'
export default {
	data() {
		return {
			info:{}
		}
	},
	onShow() {
		
	},
	onLoad(e) {
		this.id = e.id
		ajax({
			url: api.getRecruitInfo,
			type: "POST",
			data: {
				id: this.id
			},
		}).then(res => {
			if (res.status_code == "ok") {
				console.log(res)
				this.info = res.data
			}
		})
	},
	methods: {
		getDeteil(){
			ajax({
				url: api.getRecruitDetails,
				type: "POST",
				data: {
					id: this.id
				},
			}).then(res => {
				if (res.status_code == "ok") {
					console.log(res)
					this.info.hr_mobile = res.data.hr_mobile
					// this.getInfo()
					// this.info = res.data
				}else if(res.status_code == 'error'){
					this.$refs.popup.open();
				}else{
					uni.showToast({
						title:res.data.message,
						icon:'none'
					})
				}
			})
		},
		close() {
			this.$refs.popup.close();
		},
		navMembership(){
			uni.navigateTo({
				url: '../postRecruit/postRecruit'
			});
		},
	}
}
